/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: #f5f5f5;
  display: flex;
  flex-flow: column;
  align-items: center;
}
body.oh {
  overflow: hidden;
}

.heroimage {
  background-image: url(../../images/image-hero-desktop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 50vh;
  box-shadow: inset 0px 113px 151px -76px black;
}
@media only screen and (max-width: 700px) {
  .heroimage {
    background-image: url(../../images/image-hero-mobile.jpg);
  }
}

nav {
  align-items: flex-start;
  display: flex;
  flex-flow: row nowrap;
  padding: 32px 10% 0;
  position: absolute;
  height: auto;
  width: 100%;
  z-index: 2;
  transition-duration: 0.5s;
}
@media only screen and (max-width: 700px) {
  nav {
    justify-content: space-between;
  }
}
nav .mobileBurger {
  display: none;
}
@media only screen and (max-width: 700px) {
  nav .mobileBurger {
    display: inline;
  }
}
nav > img {
  margin-right: auto;
  cursor: pointer;
}
@media only screen and (max-width: 700px) {
  nav > img {
    margin-right: inherit;
  }
}
nav .pagelinks {
  flex: 0 1 auto;
  text-decoration: none;
  margin-right: 3%;
  color: #ffffff;
  transition: text-decoration 0.3s;
}
nav .pagelinks:hover {
  cursor: pointer;
  text-decoration: underline;
  text-decoration-color: #7a7a7a;
  text-decoration-thickness: 2px;
}
nav .pagelinks:last-child {
  margin-right: 0;
}
@media only screen and (max-width: 700px) {
  nav .pagelinks {
    display: none;
  }
}
nav .links {
  background-color: #fff;
  border-radius: 10px;
  color: #000;
  display: none;
  flex-flow: column;
  height: 13.25rem;
  justify-content: space-evenly;
  transform: translateX(-50%);
  left: 50%;
  position: fixed;
  top: 10%;
  width: 20.4375rem;
}
nav .links a {
  display: block;
  color: black;
  color: #000;
  text-align: left;
  display: block;
  font-size: 1.5rem;
  margin-left: 0.5rem;
  border-bottom: 2px solid #cecece;
  padding: 19px;
  font-weight: 500;
}
nav .links a:last-child {
  border-bottom: none;
}
nav .flexer {
  display: flex;
}

main {
  display: flex;
  justify-content: center;
  flex-flow: column;
  padding: 1rem;
  transform: translateY(-100px);
  border-radius: 10px;
  max-width: 800px;
  width: 80%;
}
@media only screen and (max-width: 700px) {
  main {
    width: 100%;
  }
}
main .projectMain {
  border: 1px solid #e6e6e6ab;
  background-color: #ffffff;
  width: 100%;
  height: auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  border-radius: 0.625rem;
}
main .projectMain > img {
  position: absolute;
  transform: translateY(-30px);
}
main .projectMain > h1 {
  margin-top: 3.9rem;
  text-transform: capitalize;
  padding: 0 33px;
}
main .projectMain > p {
  flex: 1 0 100%;
  text-align: center;
  margin: 0;
  color: #7a7a7a;
  font-weight: 500;
  letter-spacing: 0.02rem;
  padding: 0 3.5rem;
}
main .projectMain .buttons {
  flex: 1 0 100%;
  display: flex;
  justify-content: space-between;
  margin: 3rem;
  padding: 0 3rem;
}
main .projectMain .buttons .back_button {
  background-color: #3cb4ac;
  border: none;
  border-radius: 5rem;
  color: #ffffff;
  font-weight: 700;
  width: 12.5rem;
  font-size: 1rem;
  letter-spacing: 0.03rem;
  cursor: pointer;
  transition: background-color 0.2s;
}
main .projectMain .buttons .back_button:hover {
  background-color: #147b74;
}
main .projectMain .buttons .bookmark_button {
  border: none;
  border-radius: 5rem;
  padding: 0;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 10.875rem;
  font-weight: 700;
  color: #7a7a7a;
  font-size: 1rem;
  letter-spacing: 0.03rem;
  cursor: pointer;
  transition: opacity 0.2s;
}
@media only screen and (max-width: 700px) {
  main .projectMain .buttons .bookmark_button {
    background-color: white;
    width: 2.5rem;
  }
}
@media only screen and (max-width: 700px) {
  main .projectMain .buttons .bookmark_button > p {
    display: none;
  }
}
main .projectMain .buttons .bookmark_button > img {
  margin-right: 1rem;
}
main .projectMain .buttons .bookmark_button:hover {
  opacity: 0.7;
}
main .projectMain .buttons .bookmark_button.bookmarked {
  color: #147b74;
  transition: color 0.5s;
}
main .projectMain .buttons .bookmark_button.bookmarked > img {
  content: url(../../images/icon-bookmarked.svg);
}
main .projectStats {
  border: 1px solid #e6e6e6ab;
  display: flex;
  flex-flow: column;
  background-color: #ffffff;
  border-radius: 10px;
  margin-top: 1.625rem;
  padding: 3.125rem;
}
@media only screen and (max-width: 700px) {
  main .projectStats {
    border: 1px solid #e6e6e6ab;
    display: flex;
    flex-flow: column;
    padding: 2.5rem 2rem;
    height: auto;
  }
}
main .projectStats .statBoxes {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  height: 70px;
}
@media only screen and (max-width: 700px) {
  main .projectStats .statBoxes {
    flex-flow: column;
    height: auto;
    align-items: center;
  }
}
main .projectStats .statBoxes .stats {
  border-right: 1px solid #7a7a7a4f;
  flex: 1 0 30%;
  margin-left: 7%;
}
@media only screen and (max-width: 700px) {
  main .projectStats .statBoxes .stats {
    /* border-bottom: 1px solid #7a7a7a4f; */
    flex: 1 0 30%;
    text-align: center;
    margin-left: 0;
    margin-bottom: 20px;
    border-right: none;
  }
  main .projectStats .statBoxes .stats:nth-child(2) {
    border-bottom: 1px solid #7a7a7a4f;
    border-top: 1px solid #7a7a7a4f;
    padding: 16px 0;
  }
}
main .projectStats .statBoxes .stats > h1 {
  font-weight: 700;
  margin-bottom: 0.5rem;
}
main .projectStats .statBoxes .stats > p {
  color: #7a7a7a;
  font-weight: 500;
  margin: 0;
}
main .projectStats .statBoxes .stats:last-child {
  border-right: none;
}
main .projectStats .statBoxes .stats:first-child {
  margin-left: 0;
}
main .projectStats .progressBar {
  margin-top: 2.375rem;
}
@media only screen and (max-width: 700px) {
  main .projectStats .progressBar {
    margin-top: 0.375rem;
  }
}
main .projectStats .progressBar progress[value] {
  width: 100%;
  height: 1rem;
  appearance: none;
  border: none;
  color: #3cb4ac;
}
main .projectStats .progressBar progress[value]::-webkit-progress-bar {
  background-color: #f5f5f5;
  border-radius: 3rem;
}
main .projectStats .progressBar progress[value]::-webkit-progress-value {
  background-color: #3cb4ac;
  border-radius: 3rem;
  transition: width 0.5s;
}
main .projectStats .progressBar progress[value]::-moz-progress-bar {
  background-color: #3cb4ac;
  border-radius: 3rem;
}
main .projectStats .progressBar .progress-bar {
  background-color: #f5f5f5;
  border-radius: 3rem;
  width: 100%;
  height: 1rem;
  position: relative;
  display: block;
}
main .projectStats .progressBar .progress-bar > span {
  background-color: #3cb4ac;
  border-radius: 3rem;
  display: block;
  text-indent: -9999px;
}
main .projectFund {
  border: 1px solid #e6e6e6ab;
  display: flex;
  flex-flow: column;
  background-color: #ffffff;
  border-radius: 10px;
  margin-top: 1.625rem;
  padding: 3.125rem;
}
@media only screen and (max-width: 700px) {
  main .projectFund {
    padding: 3rem 1.125rem;
  }
}
main .projectFund > p {
  color: #7a7a7a;
  line-height: 1.5;
  font-size: 1.05rem;
  font-weight: 500;
}
main .projectFund .pledge {
  border: 2px solid #efefef;
  border-radius: 10px;
  margin-top: 1.5rem;
  padding: 2.0625rem;
  display: flex;
  flex-flow: column;
}
@media only screen and (max-width: 700px) {
  main .projectFund .pledge {
    padding: 2rem;
  }
}
main .projectFund .pledge .edition {
  display: flex;
  flex-flow: row nowrap;
  align-items: baseline;
  justify-content: space-between;
}
@media only screen and (max-width: 700px) {
  main .projectFund .pledge .edition {
    flex-flow: column;
  }
}
@media only screen and (max-width: 700px) {
  main .projectFund .pledge .edition h2 {
    font-size: 1rem;
    font-weight: 700;
    margin-bottom: 0.3rem;
  }
}
main .projectFund .pledge .edition p {
  color: #3cb4ac;
}
@media only screen and (max-width: 700px) {
  main .projectFund .pledge .edition p {
    color: #3cb4ac;
    margin: 0;
  }
}
main .projectFund .pledge .tickets {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: baseline;
}
@media only screen and (max-width: 700px) {
  main .projectFund .pledge .tickets {
    flex-flow: column;
  }
}
main .projectFund .pledge .tickets .ticketsLeft {
  font-size: 2rem;
  font-weight: 700;
  color: black;
}
main .projectFund .pledge .tickets button {
  background-color: #3cb4ac;
  border: none;
  border-radius: 3rem;
  color: #ffffff;
  font-weight: 700;
  width: 9.8125rem;
  height: 3rem;
  cursor: pointer;
  transition: background-color 0.3s;
}
main .projectFund .pledge .tickets button:hover {
  background-color: #147b74;
}
main .projectFund .pledge:last-child {
  opacity: 0.4;
}
main .projectFund .pledge:last-child button {
  background-color: black;
  pointer-events: none;
}

.selectionContainer_background {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #00000075;
  padding: 3rem;
}
.selectionContainer_background .selectionContainer {
  max-width: 730px;
  width: 100%;
  height: auto;
  margin: 0 auto;
  background-color: #ffffff;
  position: relative;
  padding: 2.5rem;
  border-radius: 10px;
  top: 12rem;
}
.selectionContainer_background .selectionContainer img {
  position: relative;
  float: right;
  cursor: pointer;
  transition-duration: 1s;
}
.selectionContainer_background .selectionContainer img:hover {
  content: url(../../images/icon-close-modal-hover.svg);
}
.selectionContainer_background .selectionContainer h1 {
  text-transform: capitalize;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  font-size: 1.7rem;
  text-transform: uppercase;
  color: black;
  font-weight: 700;
}
@media only screen and (max-width: 700px) {
  h1 {
    font-size: 1.6rem;
    text-align: center;
  }
}

h2 {
  font-size: 1.3rem;
  font-weight: 700;
}

p {
  color: #7a7a7a;
  line-height: 1.5;
  font-size: 1.05rem;
  font-weight: 500;
}

body {
  font-size: 1rem;
  font-family: "Commissioner", sans-serif;
  line-height: 1.3;
}