  
html {
    box-sizing: border-box;
    font-size: 100%;
}

*, *:before, *:after {
    box-sizing: inherit;   
}

body {
    margin: 0;
    background-color: $ltGray;
    display: flex;
    flex-flow: column;
    align-items: center;

    &.oh {
        overflow: hidden;
    }
}

// heron image styling 
.heroimage {
    background-image: url(../../images/image-hero-desktop.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 50vh;
    box-shadow: inset 0px 113px 151px -76px black;
    @media #{$mq-700-down} {
        // style in here 
        background-image: url(../../images/image-hero-mobile.jpg);
    }
}

// navigation bar styling 
nav {

    align-items: flex-start;
    display: flex;
    flex-flow: row nowrap;
    padding: 32px 10% 0;
    position: absolute;
    height: auto;
    width: 100%;
    // box-shadow: inset 0px 500px 338px -127px black;
    z-index: 2;
    transition-duration: 0.5s;
    
    @media #{$mq-700-down} {
        // style in here 
        justify-content: space-between;
    }
    .mobileBurger {
        display: none;
        @media #{$mq-700-down} {
            // style in here 
           display: inline;
        }
    }
    &>img {
        margin-right: auto;
        cursor: pointer;
        @media #{$mq-700-down} {
            // style in here 
            margin-right: inherit;
        }
    }
    .pagelinks {
        flex: 0 1 auto;
        text-decoration: none;
        margin-right: 3%;
        color: $white;
        transition: text-decoration .3s;
        &:hover {
            cursor: pointer;
            text-decoration: underline;
            text-decoration-color: $dk_gray;
            text-decoration-thickness: 2px;
        }
        &:last-child {
            margin-right: 0;
        }

        @media #{$mq-700-down} {
            // style in here 
            display: none;
        }
    }
    .links {
        background-color: #fff;
        border-radius: 10px;
        color: #000;
        display: none;
        flex-flow: column;
        height: 13.25rem;
        justify-content: space-evenly;
        transform: translateX(-50%);
        left: 50%;
        position: fixed;
        top: 10%;
        width: 20.4375rem;
        a {
            display: block;
            color: $black;
            color: #000;
            text-align: left;
            display: block;
            font-size: 1.5rem;
            margin-left: 0.5rem;
            border-bottom: 2px solid #cecece;
            padding: 19px;
            font-weight: 500;

            &:last-child {
                border-bottom: none;
            }
        }
    }
    // helper classes
    .flexer {
        display: flex;
    }

}
// main cards container styling 
main {
    display: flex;
    justify-content: center;
    flex-flow: column;
    padding: 1rem;
    transform: translateY(-100px);
    border-radius: 10px;
    max-width: 800px;
    width: 80%;
    @media #{$mq-700-down} {
        width: 100%;
    }    
    // the first card of the project styling 
    .projectMain {
        border: 1px solid #e6e6e6ab;
        background-color: $white;
        width: 100%;
        height: auto;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        border-radius: 0.625rem ;
        &>img {
            position: absolute;
            transform: translateY(-30px);
        }

        &>h1 {
            margin-top: 3.9rem;
            text-transform: capitalize;
            padding: 0 33px;
        }

        &>p {
            flex: 1 0 100%;
            text-align: center;
            margin: 0;
            color: $dk_gray;
            font-weight: 500;
            letter-spacing: 0.02rem;
            padding: 0 3.5rem;
        }

        .buttons {
            flex: 1 0 100%;
            display: flex;
            justify-content: space-between;
            margin: 3rem;
            padding: 0 3rem;
            .back_button {
                background-color: $moderate_cyan;
                border: none;
                border-radius: 5rem;
                color: $white;
                font-weight: 700;
                width: 12.5rem;
                font-size: 1rem;
                letter-spacing: 0.030rem;
                cursor: pointer;
                transition: background-color .2s;
                &:hover {
                    background-color: $dark_cyan;
                    
                }
            }

            .bookmark_button {
                border: none;
                border-radius: 5rem;
                padding: 0;
                display: flex;
                flex-flow: row nowrap;
                align-items: center;
                width: 10.875rem;
                font-weight: 700;
                color: $dk_gray;
                font-size: 1rem;
                letter-spacing: 0.030rem;
                cursor: pointer;
                transition: opacity 0.2s;
                @media #{$mq-700-down}{
                    background-color: white;
                    width: 2.5rem;

                }
                >p {
                    @media #{$mq-700-down}{
                        display: none;
    
                    }
                }
                &>img {
                    margin-right: 1rem;
                }
                &:hover {
                    opacity: 0.7;
                }   

                &.bookmarked {
                    color: $dark_cyan;
                    transition: color 0.5s;
                    >img {
                        content: url(../../images/icon-bookmarked.svg);
                    }
                }
            }

            
        }


    }

    // project stats card 
    .projectStats {
        border: 1px solid #e6e6e6ab;
        display: flex;
        flex-flow: column;
        background-color: $white;
        border-radius: 10px;
        margin-top: 1.625rem;
        padding: 3.125rem;
        @media #{$mq-700-down} {
            border: 1px solid #e6e6e6ab;
            display: flex;
            flex-flow: column;
            padding: 2.5rem 2rem;
            height: auto;
        }
        .statBoxes {
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-between;
            height: 70px;    
            @media #{$mq-700-down} {
                flex-flow: column;
                height: auto;
                align-items: center;
            }
            .stats {
                border-right: 1px solid #7a7a7a4f;
                flex: 1 0 30%;
                margin-left: 7%;
                @media #{$mq-700-down} {
                        /* border-bottom: 1px solid #7a7a7a4f; */
                        flex: 1 0 30%;
                        text-align: center;
                        margin-left: 0; 
                        margin-bottom: 20px;
                        border-right: none;
                        &:nth-child(2) {
                            border-bottom: 1px solid #7a7a7a4f;
                            border-top: 1px solid #7a7a7a4f;
                            padding: 16px 0;
                        }
                }
                &>h1 {
                    font-weight: 700;
                    margin-bottom: 0.5rem;
                }
                &>p {
                    color: $dk_gray;
                    font-weight: 500;
                    margin: 0;
                }
                &:last-child {
                    border-right: none;
                }

                &:first-child {
                    margin-left: 0;
                }
            }
        }

        // progress bar styling 
        .progressBar {
            margin-top: 2.375rem;
            @media #{$mq-700-down} {
                margin-top: 0.375rem;
            }
            progress[value] { // progress {} alone works as well but just in case we will use indetirment progress tag later I added the "[value]"
                width: 100%;
                height: 1rem;
                appearance: none; // prefixes will be detirment by the autoprefixer npm package 
                border: none;
                color: $moderate_cyan; // for background color of the IE browser as it supports is natively 
                &::-webkit-progress-bar { // to style the backgorund of the bar in the webkit browsers
                    background-color: $ltGray;
                    border-radius: 3rem;
                }
                &::-webkit-progress-value {// to style the completed bar of the progress bar
                    background-color: $moderate_cyan;
                    border-radius: 3rem;
                    transition: width .5s;
                }

                &::-moz-progress-bar {// to style the progress bar for firefox (only backgroud of progress bar can be styled )
                    background-color: $moderate_cyan;
                    border-radius: 3rem;
                }
                
            }

            // fall back progress bar for usupported or old browser 
            .progress-bar {
                background-color: $ltGray;
                border-radius: 3rem;

                width: 100%;
                height: 1rem;
                
                position: relative;
                display: block;

                &>span {
                    background-color: $moderate_cyan;
                    border-radius: 3rem;

                    display: block;
                    text-indent: -9999px;
                }
            }
        }
    }

    // projectFund
    .projectFund {
        border: 1px solid #e6e6e6ab;
        display: flex;
        flex-flow: column;
        background-color: $white;
        border-radius: 10px;
        margin-top: 1.625rem;
        padding: 3.125rem;
        @media #{$mq-700-down} {
            padding: 3rem 1.125rem;
        }

        &>p {
            color: $dk_gray;
            line-height: 1.5;
            font-size: 1.05rem;
            font-weight: 500;
        }

        .pledge {
            border: 2px solid #efefef;
            border-radius: 10px;
            margin-top: 1.5rem;
            padding: 2.0625rem;
            display: flex;
            flex-flow: column; 
            @media #{$mq-700-down} {
                padding: 2rem;
            }
            .edition {
                display: flex;
                flex-flow: row nowrap;
                align-items: baseline;
                justify-content: space-between;
                @media #{$mq-700-down} {
                    flex-flow: column;
                }
                h2 {
                    @media #{$mq-700-down} {
                        font-size: 1rem;
                        font-weight: 700;
                        margin-bottom: 0.3rem;
                    }
                }
                p {
                    color: $moderate_cyan;
                    @media #{$mq-700-down} {
                        color: #3cb4ac;
                        margin: 0;
                    }
                }
            }
            .tickets {
                display: flex;
                flex-flow: row nowrap;
                justify-content: space-between;
                align-items: baseline;
                @media #{$mq-700-down} {
                    flex-flow: column;
                }
                .ticketsLeft {
                    font-size: 2rem;
                    font-weight: 700;
                    color: $black;
                }

                button {
                    background-color: $moderate_cyan;
                    border: none;
                    border-radius: 3rem ;
                    color: $white;
                    font-weight: 700;
                    width: 9.8125rem;
                    height: 3rem;
                    cursor: pointer;
                    transition: background-color 0.3s;
                    &:hover {   
                        background-color: $dark_cyan;

                    }
                }

                
            }

            &:last-child {
                opacity: 0.4;
                button {
                    background-color: $black;
                    pointer-events: none;
                }
            }
        }
    }
}

// pledgeing panle 
.selectionContainer_background {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #00000075;
    padding: 3rem;
    .selectionContainer {
        max-width: 730px ;
        width: 100%;
        height: auto;
        margin: 0 auto;
        background-color: $white;
        position: relative;
        padding: 2.5rem;
        border-radius: 10px;
        top: 12rem;
        img {
            position: relative;
            float: right;
            cursor: pointer;
            transition-duration: 1s;
            &:hover {
                content: url(../../images/icon-close-modal-hover.svg);
            }
        }
        h1 {
            text-transform: capitalize;
        }
    }
}

